.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 1px 15px 20px -15px #0000000d;
}

.header__logo {
  width: 40px;
  padding-top: 2px;
}

.nav {
  transition: transform .3s;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__item-link {
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__item-link:hover, .nav__item-link:active, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 30px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

.card-container {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card-container .card-container {
  grid-template-columns: repeat(4, 200px);
}

@media (width <= 487px) {
  .card-container {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) and (width <= 767px) {
  .card-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .card-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .card-container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 100%;
  max-width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  transition: transform .3s;
  display: flex;
}

.card__picture {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__stars-text {
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: block;
}

.card__button:hover, .card__button:active {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  cursor: pointer;
  z-index: 2;
  transform: scale(1.2);
}

.card__title:hover {
  color: #34568b;
}

